      <div id="page-wrapper">
        <div class="container-fluid">

          <!-- Page Heading -->
          <!-- /.row -->
          <div class="row center-block" style="width: 70%;">
            <div class="col-md-8">
              <div class="panel with-nav-tabs panel-primaryes" style="background-color: white">
                <div class="panel-heading">
                  <ul class="nav nav-tabs" style="background-color: #c0c4c7">
                    <li v-for="i in tabs.name.length" :class="[tab == tabs.name[i-1].toLowerCase()? 'active' : 'dropdown']" @click="tab=tabs.name[i-1].toLowerCase()" style="font-size: 15px"><a data-toggle="tab"><span :class="tabs.fa[i-1]"></span> {{tabs.name[i-1]}}</a></li>
                  </ul>
                </div>

                <div class="panel-body">
                  <div class="tab-content">
                    <div class="tab-pane fade in active">
                        <component :is="tab" :qrcode="qrcode"></component>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4" style="background-color: white; border-radius: 5px">
              <div class="container-fluid">
                <div class="row">
                 <img v-if="qrcode.url.length == 0" src="https://www.qr-code-generator.com/wp-content/themes/qr/images/banners/frames/websiteQRCode_noFrame.png" style="width: 180px; height: 180px; opacity: 0.2"/>
                  <a :href="qrcode.url" target="_blank" download>
                   <img v-if="qrcode.url.length != 0" :src="qrcode.url"/>
                  </a>
                </div>
                <div class="row">
                  <button class="btn btn-info disabled" @click="printImg" style="margin-bottom: 10px">Print</button>
                </div>
                <div class="row">
                  <a data-toggle="modal" data-target="#exampleModal" role="button">Embeded qr code</a>
