import {mix} from './mixins'
export default{
  mixins: [mix],
  data(){
    return{
      qrcode: {
        sms:{
          phone: '',
          text: ''
        },
        email: {
          text: '',
          email: '',
          subject: ''
        },
        text:{
          test: ''
        },
        link:{
          url: ''
        },
        vcard:{
          firstname: '',
          lastname: '',
          phone: '',
          fax: '',
          email: '',
          company: '',
          job: '',
          city: '',
          street: '',
          state: '',
          country: ''
        },
        url: ''
      },
      tabs:{
        name:['Url','VCard', 'Message', 'Email', 'SMS'],
        fa: ['fa fa-gamepad', 'fa fa-cloud', 'fa fa-beer', 'fa fa-money']
      },
      tab: 'url',
      code: null
    }
  },
  components: {
    url: () => loadWidget('Url'),
    vcard: () => loadWidget('Vcard'),
    message: () => loadWidget('Text'),
    email: () => loadWidget('Email'),
    sms: () => loadWidget('Sms')
  },
  mounted(){
    let code = JSON.parse(localStorage.getItem('qr-barcode-generator'));
    let tab = localStorage.getItem('qr-barcode-generator-tab')
    if(code && tab) {
      this.qrcode = code
      this.tab = tab
    }
  },
  methods:{
    printImg(){
      var win = window.open('');
      win.document.write('<img src="' + this.qrcode.url + '" onload="window.print();window.close()" />');
      win.focus();
    }
  }
}
function loadWidget(widget){
  widget += '.vue'
  console.log("Loaded " + widget);
  return System.import('./components/' + widget);
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #c0c4c7;
  margin-top: 60px;
}
</style>



